
body{
  background-color: #EEE;
  margin-top: 10000;
}

.header{
  align: center;
  background-color:#999da6

}
.head{
  align: center;
  font-style: italic;
  font-family: arial;
  margin-left: 10px;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}
.dropdown {
  position: relative;
  display: inline-block;
}
.dropbtn{

}
.center{
    align: center;
    clear: both;
    
    display: block;
    margin-left: auto;
    margin-right: auto;

}
button {
  background-color: blue;
  border: none;
  color: white;
  text-align: center;
  font-size: 16px;
  margin: 4px 0px;
  opacity: 0.6;
  transition: 0.3s;
  display: inline-block;
  text-decoration: none;
  cursor: pointer;
  float: left;
  width: 25%;
  padding: 8px;

}
.apparea{
  align: center;
  position: absolute;
  display: block;
  clear: both;
  padding-top: 10px;


}
.bar{

  padding: 2px;
  background-color: blue;
  margin-left: 1px;
  margin-right: 1px;
  display: inline-block;

  width:6%;
  float: left;

}
button:hover {opacity: 1}
.dropdown:hover .dropdown-content {display: block;}
.dropdown:hover .dropbtn {opacity: 1}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  align-self: center;
}
.footer{
  clear: both;
}
